.Title {
  top: 50px;
  max-width: 1920px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.Table {
  table-layout: fixed;
  margin: auto;
  border-collapse: separate;
  /* border-spacing: 50px 0; */
  width: 100%;
}

.Modal {
